
import React, { useContext, useEffect } from 'react';
import { useForm, FormContextValues } from 'react-hook-form';
import { useMachine } from '@xstate/react';
import { Interpreter } from 'xstate';

import { StoreContext } from './App';
import { formMachine } from './machines/form';
import { Table, Tr, Th, Td } from './components/Table';
import { getAssetAccounts } from './Home';
import { TopAppBar } from './components/TopAppBar';

type OwnProps = {
  storeService: Interpreter<any, any, any, any>;
};

const fieldInstitutionId = 'institutionId';
const fieldAssetId = 'assetId';
const fieldName = 'name';

type FormValues = {
  [fieldInstitutionId]: string;
  [fieldAssetId]: string;
  [fieldName]: string;
};

type FormMachineShape = {
  form: FormContextValues<FormValues>;
};

export const AssetAccounts: React.FC<OwnProps> = ({ storeService }) => {
  const store = useContext(StoreContext);
  const form = useForm<FormValues>();
  const [formMachineState, formMachineSend] = useMachine<FormMachineShape, any>(
    formMachine,
    {
      devTools: true,
      context: {
        form,
      },
    }
  );

  const onSubmit = (values: FormValues) => {
    // console.log(values);
    formMachineSend('SUBMIT');
  };

  useEffect(() => {
    if (formMachineState.matches('submitted')) {
      storeService.send('UPDATE_DATA', {
        actionType: 'create',
        entity: 'assetAccounts',
        data: formMachineState.context.form.getValues(),
      });
      formMachineSend('REINIT');
    }
  }, [formMachineState, formMachineSend, storeService]);

  const deleteAssetAccount = (id: string) => {
    storeService.send({
      type: 'UPDATE_DATA',
      actionType: 'delete',
      entity: 'assetAccounts',
      data: {
        id,
      },
    });
  };

  return (
    <div>
      <h1>Asset accounts {getAssetAccounts(store)?.length}</h1>
      <form onSubmit={form.handleSubmit(onSubmit)}>
        <Table>
          <thead>
            <Tr>
              <Th colspan={4}></Th>
              <Th>Used in # of</Th>
            </Tr>
            <Tr>
              <Th>id</Th>
              <Th>asset</Th>
              <Th>institution</Th>
              <Th>name</Th>
              <Th>reports</Th>
            </Tr>
          </thead>
          <tbody>
            <Tr>
              <Td></Td>
              <Td>
                <select
                  name={fieldAssetId}
                  ref={form.register({
                    required: 'Required',
                    // pattern: {
                    //   value: /^(BANK|BROKER|PERSON|PHYSICAL_LOCATION)$/i,
                    //   message: 'invalid type',
                    // },
                  })}
                >
                  {store?.data.assets.map((v) => (
                    <option key={v.id} value={v.id}>
                      {v.class} {v.subclass} {v.name}
                    </option>
                  ))}
                </select>
              </Td>
              <Td>
                <select
                  name={fieldInstitutionId}
                  ref={form.register({
                    required: 'Required',
                    // pattern: {
                    //   value: /^(BANK|BROKER|PERSON|PHYSICAL_LOCATION)$/i,
                    //   message: 'invalid type',
                    // },
                  })}
                >
                  {store?.data.institutions.map((v) => (
                    <option key={v.id} value={v.id}>
                      {v.type} {v.name}
                    </option>
                  ))}
                </select>
              </Td>
              <Td>
                <input
                  name={fieldName}
                  ref={form.register({ required: 'Required' })}
                />
                <br />
                {form.errors.name?.message}
              </Td>
              <Td></Td>
              <Td>
                <button type="submit">Save</button>
              </Td>
            </Tr>
            {getAssetAccounts(store)?.map((assetAccount) => {
              // actually store is definitely not undefined here
              // as `getAssetAccounts` has returned data
              if (store === undefined) {
                return null;
              }
              const assetAccountStatesWithThisAssetAccount = store?.data.assetAccountStates.filter(
                (aas) => aas.assetAccountId === assetAccount.id
              );
              return (
                <Tr key={assetAccount.id}>
                  <Td>{assetAccount.id}</Td>
                  <Td>
                    {
                      store.data.assets.find(
                        (a) => a.id === assetAccount.assetId
                      )?.name
                    }
                  </Td>
                  <Td>
                    {
                      store.data.institutions.find(
                        (a) => a.id === assetAccount.institutionId
                      )?.name
                    }
                  </Td>
                  <Td>{assetAccount.name}</Td>
                  <Td>{assetAccountStatesWithThisAssetAccount.length}</Td>
                  <Td>
                    {assetAccountStatesWithThisAssetAccount.length === 0 && (
                      <button
                        type="button"
                        onClick={() => deleteAssetAccount(assetAccount.id)}
                      >
                        delete
                      </button>
                    )}
                  </Td>
                </Tr>
              );
            })}
          </tbody>
        </Table>
      </form>
    </div>
  );
};
