import React, { useContext, useState } from 'react';
import { Interpreter } from 'xstate';
import {
  SortableContainer,
  SortableElement,
  SortEndHandler,
  SortEnd,
} from 'react-sortable-hoc';
import arrayMove from 'array-move';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import DragHandleIcon from '@material-ui/icons/DragHandle';

import { getInstitutions } from './Home';
import { StoreContext } from './App';
import { StoreEvent } from './machines/store';
type OwnProps = {
  storeService: Interpreter<any, any, StoreEvent, any>;
};

const SortableItem = SortableElement(({ value }: { value: any }) => (
  <ListItem ContainerComponent="div">
    <ListItemText primary={value} />
    <ListItemSecondaryAction>
      <DragHandleIcon />
    </ListItemSecondaryAction>
  </ListItem>
));

const SortableContainer1 = SortableContainer(
  ({ children }: { children: any }) => {
    return <List component="div">{children}</List>;
  }
);

export const InstitutionsSort: React.FC<OwnProps> = (props) => {
  const store = useContext(StoreContext);
  const institutions = getInstitutions(store)!;

  const [items, setItems] = useState(institutions);

  const onSortEnd: SortEndHandler = (sort: SortEnd) => {
    const newArr = arrayMove(items, sort.oldIndex, sort.newIndex);
    setItems(newArr);
    newArr.forEach((inst, idx) => {
      props.storeService.send('UPDATE_DATA', {
        actionType: 'update',
        entity: 'institutions',
        data: {
          ...inst,
          sortIndex: idx,
        },
      });
    });
  };

  return (
    <SortableContainer1 onSortEnd={onSortEnd}>
      {items.map((value, index) => (
        <SortableItem
          key={`item-${value.id}`}
          index={index}
          value={value.name}
        />
      ))}
    </SortableContainer1>
  );
};
