import React from 'react';
import { BrowserRouter, Routes, Route } from 'react-router-dom';
import { useMachine } from '@xstate/react';
import CssBaseline from '@material-ui/core/CssBaseline';

import * as styles from './App.treat';
// import { fetchMachine } from './machines/fetch';
import { storeMachine } from './machines/store';
import { fintrack } from '../typings/fintrack';
import { Charts } from './Charts';
import { BottomAppBar } from './components/BottomAppBar';
import { AssetsAndReports } from './AssetsAndReports';
import { ImportExport } from './ImportExport';

export const StoreContext = React.createContext<fintrack.JSONShape | undefined>(
  undefined
);

const App: React.FC = () => {
  const [state1, send, storeService] = useMachine(storeMachine, {
    devTools: true,
  });

  return (
    <StoreContext.Provider value={state1.context.db?.getState()}>
      <BrowserRouter>
        <React.Fragment>
          <CssBaseline />
          <div className={styles.root}>
            <div className={styles.content}>
              <Routes>
                <Route element={<Charts />} />
                <Route
                  path="assets-and-reports/*"
                  element={<AssetsAndReports storeService={storeService} />}
                />
                <Route
                  path="import-export"
                  element={
                    <ImportExport
                      state={state1}
                      send={send}
                      storeService={storeService}
                    />
                  }
                />
              </Routes>
            </div>
            <BottomAppBar />
          </div>
        </React.Fragment>
      </BrowserRouter>
    </StoreContext.Provider>
  );
};

export default App;
