import React, { useContext } from 'react';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import { Link } from 'react-router-dom';
import { Button } from '@material-ui/core';
import { Sort } from '@material-ui/icons';

import * as styles from './Insitutions.treat';
import { StoreContext } from './App';
import { getAssetAccounts, getInstitutions } from './Home';

export const Insitutions: React.FC = () => {
  const store = useContext(StoreContext);

  return (
    <div className={styles.root}>
      <Button
        startIcon={<Sort />}
        to="sort"
        component={Link}
        variant="contained"
        color="secondary"
        size="small"
      >
        sort
      </Button>

      <Button
        to="create"
        component={Link}
        variant="contained"
        color="secondary"
        size="small"
      >
        add
      </Button>

      <Table stickyHeader>
        <TableHead>
          <TableRow>
            <TableCell>type</TableCell>
            <TableCell>name</TableCell>
            <TableCell>used in</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {getInstitutions(store)?.map((inst) => {
            const assetAccountsWithCurrentInstitution = getAssetAccounts(
              store
            )!.filter((aa) => aa.institutionId === inst.id);
            return (
              <TableRow hover key={inst.id} component={Link} to={inst.id}>
                <TableCell>{inst.type}</TableCell>
                <TableCell>{inst.name}</TableCell>
                {/* <TableCell>{inst.sortIndex}</TableCell> */}
                <TableCell>
                  {assetAccountsWithCurrentInstitution.length}
                </TableCell>
                {/* <TableCell>
                    {assetAccountsWithCurrentInstitution.length === 0 && (
                      <button
                        type="button"
                        onClick={() => deleteInstitution(inst.id)}
                      >
                        delete
                      </button>
                    )}
                  </TableCell> */}
              </TableRow>
            );
          })}
        </TableBody>
      </Table>
    </div>
  );
};
