import React, { useContext, useEffect } from 'react';
import { useForm, FormContextValues } from 'react-hook-form';
import { useMachine } from '@xstate/react';
import { Interpreter } from 'xstate';

import { StoreContext } from './App';
import { formMachine } from './machines/form';
import { ALL_ASSET_TYPES } from './constants';
import { Table, Tr, Th, Td } from './components/Table';
import { StoreEvent } from './machines/store';
import { getAssetAccounts } from './Home';
import { TopAppBar } from './components/TopAppBar';

type OwnProps = {
  storeService: Interpreter<any, any, StoreEvent, any>;
};

const fieldClass = 'class';
const fieldSubclass = 'subclass';
const fieldName = 'name';
const fieldCurrency = 'currency';

type FormValues = {
  [fieldClass]: string;
  [fieldSubclass]: string;
  [fieldName]: string;
  [fieldCurrency]: string;
};

type FormMachineShape = {
  form: FormContextValues<FormValues>;
};

export const Assets: React.FC<OwnProps> = ({ storeService }) => {
  const store = useContext(StoreContext);
  const form = useForm<FormValues>();
  const [formMachineState, formMachineSend] = useMachine<FormMachineShape, any>(
    formMachine,
    {
      devTools: true,
      context: {
        form,
      },
    }
  );

  const onSubmit = (values: FormValues) => {
    // console.log(values);
    formMachineSend('SUBMIT');
  };

  useEffect(() => {
    if (formMachineState.matches('submitted')) {
      storeService.send('UPDATE_DATA', {
        actionType: 'create',
        entity: 'assets',
        data: formMachineState.context.form.getValues(),
      });
      formMachineSend('REINIT');
    }
  }, [formMachineState, formMachineSend, storeService]);

  const deleteAsset = (id: string) => {
    storeService.send({
      type: 'UPDATE_DATA',
      actionType: 'delete',
      entity: 'assets',
      data: {
        id,
      },
    });
  };

  return (
    <div>
      <h1>Assets {store?.data.assets?.length}</h1>
      <form onSubmit={form.handleSubmit(onSubmit)}>
        <Table>
          <thead>
            <Tr>
              <Th colspan={5}></Th>
              <Th>Used in # of</Th>
            </Tr>
            <Tr>
              <Th>ID</Th>
              <Th>class</Th>
              <Th>subclass</Th>
              <Th>name</Th>
              <Th>currency</Th>
              <Th>asset accounts</Th>
            </Tr>
          </thead>
          <tbody>
            <Tr>
              <Td></Td>
              <Td>
                <select
                  name={fieldClass}
                  ref={form.register({
                    required: 'Required',
                    // pattern: {
                    //   value: /^(BANK|BROKER|PERSON|PHYSICAL_LOCATION)$/i,
                    //   message: 'invalid type',
                    // },
                  })}
                >
                  {ALL_ASSET_TYPES.map((v) => (
                    <option value={v}>{v}</option>
                  ))}
                </select>
              </Td>
              <Td>
                <input
                  name={fieldSubclass}
                  ref={form.register({ required: 'Required' })}
                />
                {form.errors.subclass?.message}
              </Td>
              <Td>
                <input
                  name={fieldName}
                  ref={form.register({ required: 'Required' })}
                />
                <br />
                {form.errors.name?.message}
              </Td>
              <Td>
                <input
                  name={fieldCurrency}
                  ref={form.register({ required: 'Required' })}
                />
                <br />
                {form.errors.currency?.message}
              </Td>
              <Td></Td>
              <Td>
                <button type="submit">Save</button>
              </Td>
            </Tr>
            {store?.data.assets?.map((asset) => {
              const assetAccountsWithCurrentAsset = getAssetAccounts(
                store
              )?.filter((aa) => aa.assetId === asset.id)!;
              return (
                <Tr key={asset.id}>
                  <Td>{asset.id}</Td>
                  <Td>{asset.class}</Td>
                  <Td>{asset.subclass}</Td>
                  <Td>{asset.name}</Td>
                  <Td>{asset.currency}</Td>
                  <Td>{assetAccountsWithCurrentAsset.length}</Td>
                  <Td>
                    {assetAccountsWithCurrentAsset.length === 0 && (
                      <button
                        type="button"
                        onClick={() => deleteAsset(asset.id)}
                      >
                        delete
                      </button>
                    )}
                  </Td>
                </Tr>
              );
            })}
          </tbody>
        </Table>
      </form>
    </div>
  );
};
