import BottomNavigation from '@material-ui/core/BottomNavigation';
import BottomNavigationAction from '@material-ui/core/BottomNavigationAction';
import ShowChartOutlinedIcon from '@material-ui/icons/ShowChartOutlined';
import TableChartOutlinedIcon from '@material-ui/icons/TableChartOutlined';
import ImportExportOutlinedIcon from '@material-ui/icons/ImportExportOutlined';
import SettingsIcon from '@material-ui/icons/Settings';
import React, { useEffect } from 'react';
import { createStyles, Theme, makeStyles } from '@material-ui/core/styles';
import _ from 'lodash';
import { AppBar } from '@material-ui/core';
import { Link, useLocation } from 'react-router-dom';

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    text: {
      padding: theme.spacing(2, 2, 0),
    },
    paper: {
      paddingBottom: 50,
    },
    list: {
      marginBottom: theme.spacing(2),
    },
    subheader: {
      backgroundColor: theme.palette.background.paper,
    },
    appBar: {
      top: 'auto',
      bottom: 0,
    },
    grow: {
      flexGrow: 1,
    },
    fabButton: {
      position: 'absolute',
      zIndex: 1,
      top: -30,
      left: 0,
      right: 0,
      margin: '0 auto',
    },
    // list: {
    //   width: 250,
    // },
    fullList: {
      width: 'auto',
    },
  })
);

const locations = {
  0: '/',
  1: '/assets-and-reports',
  2: '/import-export',
} as const;

type Locations = keyof typeof locations;
const locs = _.values(locations);

const doesMatch = (l: string, pathname: string): boolean =>
  l === '/' ? pathname === '/' : pathname.startsWith(l);

export const BottomAppBar: React.FC = () => {
  const classes = useStyles();
  const location = useLocation();

  const [value, setValue] = React.useState<Locations | undefined>(undefined);

  useEffect(() => {
    if (!locs.some((l) => doesMatch(l, location.pathname))) {
      setValue(undefined);
    }
    const idx = locs.findIndex((l) => doesMatch(l, location.pathname));
    if (idx !== -1) {
      setValue(idx as Locations);
    }
  }, [location.pathname, value]);

  return (
    <AppBar position="fixed" color="primary" className={classes.appBar}>
      <BottomNavigation
        value={value}
        onChange={(event, newValue) => {
          console.log({ event, newValue });
          setValue(newValue);
        }}
        // showLabels

      >
        <BottomNavigationAction
          component={Link}
          to="."
          label="Charts"
          icon={<ShowChartOutlinedIcon />}
        />
        <BottomNavigationAction
          component={Link}
          to="assets-and-reports"
          label="Data"
          icon={<TableChartOutlinedIcon />}
        />
        <BottomNavigationAction
          component={Link}
          to="import-export"
          label="Export/import"
          icon={<SettingsIcon />}
        />
      </BottomNavigation>
    </AppBar>
  );
};
