import React from 'react';

import * as styles from './Table.treat';

export const Table: React.FC = ({ children }) => {
  return (
    <table className={styles.table}>
      {children}
    </table>
  )
}

export const Tr: React.FC = ({ children }) => {
  return (
    <tr className={styles.tr}>
      {children}
    </tr>
  )
}

export const Td: React.FC<{ colspan?: number }> = ({ children, colspan }) => {
  return (
    <td className={styles.td} colSpan={colspan}>
      {children}
    </td>
  )
}

export const Th: React.FC<{ colspan?: number }> = ({ children, colspan }) => {
  return (
    <th className={styles.th} colSpan={colspan}>
      {children}
    </th>
  )
}
