const configureTimelineChart = (chart: any, createValueAxis: boolean = true) => {
  chart.paddingRight = 20;
  // chart.dateFormatter.dateFormat = "MMM yyyy";

  let dateAxis = chart.xAxes.push(new am4charts.DateAxis());
  dateAxis.renderer.grid.template.location = 0;
  // dateAxis.renderer.grid.template.location = 0.5;
  dateAxis.renderer.minGridDistance = 50;

  // NOTE: range selection plugin
  // let selector = new am4plugins_rangeSelector.DateAxisRangeSelector();
  // selector.container = document.getElementById("selectordiv")!;
  // selector.axis = dateAxis;

  // NOTE: https://www.amcharts.com/docs/v4/concepts/axes/date-axis/#Formatting_date_and_time
  dateAxis.dateFormats.setKey("day", "MMMM dt");
  dateAxis.dateFormats.setKey("month", "MMM yyyy");
  dateAxis.periodChangeDateFormats.setKey("month", "[bold]MMM yyyy");

  // NOTE: https://www.amcharts.com/docs/v4/tutorials/using-fill-rules-on-a-date-axis/
  dateAxis.renderer.axisFills.template.disabled = false;
  dateAxis.renderer.axisFills.template.fill = am4core.color("#1AB0F1");
  dateAxis.renderer.axisFills.template.fillOpacity = 0.1;

  // dateAxis.startLocation = 0.5;
  // dateAxis.endLocation = 0.5;
  
  // if createValueAxis) {}
  let valueAxis = chart.yAxes.push(new am4charts.ValueAxis());
  // valueAxis.tooltip!.disabled = true;
  valueAxis.renderer.minWidth = 35;

  let cursor = new am4charts.XYCursor();
  cursor.xAxis = dateAxis;
  cursor.fullWidthLineX = true;
  cursor.lineX.strokeWidth = 0;
  cursor.lineX.fill = am4core.color("#8F3985");
  cursor.lineX.fillOpacity = 0.1;
  chart.cursor = cursor;

  let scrollbarX = new am4charts.XYChartScrollbar();
  // scrollbarX.series.push(allSeries[0]);
  // scrollbarX.series.push(allSeries[1]);
  chart.scrollbarX = scrollbarX;

  chart.legend = new am4charts.Legend();

  // NOTE: zoom to last year by default.
  // https://www.amcharts.com/docs/v4/tutorials/pre-zooming-an-axis/#Setting_up_pre_zoom
  // chart.events.on("ready", function () {
  //   const toDate = new Date();
  //   const fromDate = new Date();
  //   fromDate.setFullYear(toDate.getFullYear() - 1);
  //   dateAxis.zoomToDates(fromDate, toDate);
  // });
};


const TotalByCurrenciesChart: React.FC = () => {
  const store = useContext(StoreContext);
  const currencies: fintrack.Currency[] = ['USD', 'EUR', 'RUB'];
  const reports = store?.data.reports

  function create(dataPerCurrency: any) {
    let chart = am4core.create("total_by_currencies_chart", am4charts.XYChart);
    configureTimelineChart(chart);
    // data series
    let allSeries = _.map(dataPerCurrency, (seriesData) => {
      let series = chart.series.push(new am4charts.LineSeries());
      series.data = seriesData.points;
      series.dataFields.dateX = "date";
      series.dataFields.valueY = "value";
      series.name = seriesData.currency;
      series.strokeWidth = 3;
      series.tooltipText = "{valueY.value} ({valueY.change})";
      series.bullets.push(new am4charts.CircleBullet());
      return series;
    });
    
    return chart;
  }

  const dataPerCurrency = currencies.map((currency) => {
    const totalsByCurrency = _.compact(
      reports?.map((report) => {
        return {
          date: new Date(report.datetime),
          value: parseFloat((
            getTotalForReport(
              store,
              report,
              currency,
              (a) => a.name === currency
            ) || 0
          ).toFixed(0)),
        }
      })
    );
    return {
      currency: currency,
      points: totalsByCurrency || [],
    };
  });

  useEffect(() => {
    const chart = create(dataPerCurrency);
    return () => {chart.dispose()};
  });

  return (
    <div id="total_by_currencies_chart" style={{ width: "100%", height: "500px", marginBottom: "100px" }}></div>
    // <div id="selectordiv" style={{ width: "100%", height: "30px", marginBottom: "100px" }} />
  );
}


const TotalInCurrenciesChart = (props: {stacked: boolean}) => {
  const store = useContext(StoreContext);
  const currencies: fintrack.Currency[] = ['USD', 'EUR', 'RUB'];
  const reports = store?.data.reports

  function create(dataPerCurrency: any) {
    let chart = am4core.create("total_in_currencies_chart", am4charts.XYChart);
    configureTimelineChart(chart, !props.stacked);
    let allSeries = _.map(dataPerCurrency, (seriesData, index: number) => {  
      let series = chart.series.push(new am4charts.LineSeries());
      series.data = seriesData.points;
      series.dataFields.dateX = "date";
      series.dataFields.valueY = "value";
      series.name = seriesData.currency;
      series.strokeWidth = 3;
      series.tooltipText = "{valueY.value} ({valueY.change})";
      series.bullets.push(new am4charts.CircleBullet());

      if (props.stacked) {
        let valueAxis = chart.yAxes.push(new am4charts.ValueAxis());
        series.yAxis = valueAxis;
        valueAxis.renderer.line.strokeOpacity = 1;
        valueAxis.renderer.line.stroke = series.stroke;
        valueAxis.renderer.grid.template.stroke = series.stroke;
        valueAxis.renderer.grid.template.strokeOpacity = 0.1;
        valueAxis.renderer.labels.template.fill = series.stroke;
        valueAxis.renderer.minGridDistance = 20;
        valueAxis.align = "right";
        if (index === 0) {
          valueAxis.marginBottom = 20;
        } else {
          if (index === dataPerCurrency.length - 1) {
            valueAxis.marginTop = 20;
          } else {
            valueAxis.marginTop = 10;
            valueAxis.marginBottom = 10;
          }
        };
      }
      return series;
    });

    if (props.stacked) {
      chart.leftAxesContainer.layout = "vertical";
    };

    return chart;
  }

  const dataPerCurrency = currencies.map((currency) => {
    const totalsInCurrency = _.compact(
      reports?.map((report) => {
        return {
          date: new Date(report.datetime),
          value: parseFloat((
            getTotalForReport(store, report, currency) || 0
          ).toFixed(0)),
        }
      })
    );
    return {
      currency: currency,
      points: totalsInCurrency || [],
    };
  });

  useEffect(() => {
    const chart = create(dataPerCurrency);
    return () => {chart.dispose()};
  });

  return (
    <div id="total_in_currencies_chart" style={{ width: "100%", height: "500px", marginBottom: "100px" }}></div>
    // <div id="selectordiv" style={{ width: "100%", height: "30px", marginBottom: "100px" }} />
  );
}


const TotalCashDistibutionByCurrencyChart: React.FC = () => {
  const containerId = "total_cash_distribution_by_currency_chart";
  const store = useContext(StoreContext);
  const currencies: fintrack.Currency[] = ['USD', 'EUR', 'RUB'];
  const reports = store?.data.reports

  function create(data: {currency: string, totalInUSD: number}[]) {
    let chart = am4core.create(containerId, am4charts.PieChart);
    chart.data = data;

    chart.innerRadius = am4core.percent(30);

    let series = chart.series.push(new am4charts.PieSeries());    
    series.dataFields.value = "totalInUSD";
    series.dataFields.category = "currency";

    series.ticks.template.disabled = true;

    series.slices.template.strokeWidth = 2;
    series.slices.template.stroke = am4core.color("#000");
    series.slices.template.tooltipText = "{value.value.formatNumber('#,###.')} USD";

    series.alignLabels = false;
    series.labels.template.text = "{category}: {value.percent.formatNumber('#.0')}%";
    series.labels.template.radius = am4core.percent(-35);
    series.labels.template.fill = am4core.color("white");    
    return chart;
  }

  const lastReport = _.maxBy(store?.data.reports, (r) => r.datetime);

  let data: {currency: string, totalInUSD: number}[] = [];
  if (lastReport) {
    data = currencies.map((currency) => {
      return {
        currency: currency,
        totalInUSD: getTotalForReport(
          store,
          lastReport,
          'USD',
          (a: fintrack.Asset) => a.class === 'CURRENCY' && a.name === currency
        ) || 0,
      };
    });
  };
  console.log(data);

  useEffect(() => {
    const chart = create(data);
    return () => {chart.dispose()};
  });

  return (
    <div id={containerId} style={{ width: "100%", height: "500px", marginBottom: "100px" }}></div>
  );
}


