import React, { useEffect, useContext } from 'react';
import { useParams } from 'react-router-dom';
import * as yup from 'yup';
import { Interpreter } from 'xstate';

import { StoreEvent } from './machines/store';
import { FormContextValues, useForm } from 'react-hook-form';
import { Table, Tr, Th, Td } from './components/Table';
import { ALL_INSTITUTION_TYPES } from './constants';
import { getAssetAccounts } from './Home';
import { useMachine } from '@xstate/react';
import { StoreContext } from './App';
import { formMachine } from './machines/form';
import { fintrack } from '../typings/fintrack';

type OwnProps = {
  storeService: Interpreter<any, any, StoreEvent, any>;
};

const fieldType = 'type';
const fieldName = 'name';
const fieldSortIndex = 'sortIndex';

type FormValues = {
  [fieldType]: string;
  [fieldName]: string;
  [fieldSortIndex]: number;
};

type FormMachineShape = {
  form: FormContextValues<FormValues>;
};

const yupInstitution = yup.object({
  [fieldType]: yup.string().required('required'),
  [fieldName]: yup.string().required('required'),
  [fieldSortIndex]: yup
    .number()
    .integer()
    .moreThan(-1, 'required posititve number')
    .required('required'),
});

export const AddEditInstitution: React.FC<OwnProps> = ({ storeService }) => {
  const params = useParams();
  const store = useContext(StoreContext);

  let inst: fintrack.Institution | undefined;
  if (params.institutionId !== undefined) {
    inst = store?.data.institutions.find((i) => i.id === params.institutionId);
  }

  const form = useForm<FormValues>({
    validationSchema: yupInstitution,
    defaultValues: {
      type: inst?.type,
      name: inst?.name,
      sortIndex: inst?.sortIndex,
    },
  });
  const [formMachineState, formMachineSend] = useMachine<FormMachineShape, any>(
    formMachine,
    {
      devTools: true,
      context: {
        form,
      },
    }
  );

  const onSubmit = (values: FormValues) => {
    console.log('________', values);
    formMachineSend('SUBMIT');
  };

  const deleteInstitution = () => {
    console.log('----delete', inst?.id)
    storeService.send({
      type: 'UPDATE_DATA',
      actionType: 'delete',
      entity: 'institutions',
      data: {
        id: inst?.id,
      },
    });
  };

  useEffect(() => {
    if (formMachineState.matches('submitted')) {
      if (inst === undefined) {
        storeService.send('UPDATE_DATA', {
          actionType: 'create',
          entity: 'institutions',
          data: yupInstitution.validateSync(
            formMachineState.context.form.getValues()
          ),
        });
      } else {
        storeService.send('UPDATE_DATA', {
          actionType: 'update',
          entity: 'institutions',
          data: {
            id: inst.id,
            ...yupInstitution.validateSync(
              formMachineState.context.form.getValues()
            ),
          },
        });
      }
      formMachineSend('REINIT');
    }
  }, [formMachineState, formMachineSend, storeService, inst]);

  const assetAccountsWithCurrentInstitution = getAssetAccounts(store)!.filter(
    (aa) => aa.institutionId === inst?.id
  );

  return (
    <div>
      <form onSubmit={form.handleSubmit(onSubmit)}>
        Insitution {inst?.id}
        <Table>
          <thead>
            <Tr>
              <Th colspan={3}></Th>
              <Th>Used in # of</Th>
              <Th></Th>
            </Tr>
            <Tr>
              <Th>ID</Th>
              <Th>type</Th>
              <Th>name</Th>
              <Th>sort index</Th>
              <Th>asset accounts</Th>
              <Th>action</Th>
            </Tr>
          </thead>
          <tbody>
            <Tr>
              <Td></Td>
              <Td>
                <select
                  name={fieldType}
                  ref={form.register({
                    required: 'Required',
                    // pattern: {
                    //   value: /^(BANK|BROKER|PERSON|PHYSICAL_LOCATION)$/i,
                    //   message: 'invalid type',
                    // },
                  })}
                >
                  {ALL_INSTITUTION_TYPES.map((v) => (
                    <option value={v}>{v}</option>
                  ))}
                </select>
              </Td>
              <Td>
                <input
                  name={fieldName}
                  ref={form.register({ required: 'Required' })}
                />
                {form.errors.name?.message}
              </Td>
              <Td>
                <input
                  name={fieldSortIndex}
                  ref={form.register({ required: 'Required' })}
                />
                {form.errors.sortIndex?.message}
              </Td>
              <Td>
                used in # of asset accounts{' '}
                {assetAccountsWithCurrentInstitution.length}
                {assetAccountsWithCurrentInstitution.length === 0 && (
                  <button type="button" onClick={deleteInstitution}>Delete</button>
                )}
              </Td>
              <Td>
                <button type="submit">Save</button>
              </Td>
            </Tr>
          </tbody>
        </Table>
      </form>
    </div>
  );
};
