import React from 'react';
import { Interpreter } from 'xstate';
import { useNavigate } from 'react-router';
import Button from '@material-ui/core/Button';

import { StoreEvent, StoreContext } from './machines/store';

type Props = {
  state: any;
  send: any;
  storeService: Interpreter<StoreContext, any, StoreEvent, any>;
};

export const ImportExport: React.FC<Props> = ({
  state: state1,
  send,
  storeService,
}) => {
  const navigate = useNavigate();

  const handleFile = (e: React.ChangeEvent<HTMLInputElement>) => {
    var reader = new FileReader();
    reader.onload = onReaderLoad;
    reader.readAsText(e.target.files![0]);

    function onReaderLoad(event: ProgressEvent<FileReader>) {
      console.log(event.target!.result);
      var obj = JSON.parse(event.target!.result as string);
      // alert_data(obj.name, obj.family);
      send('LOAD_FROM_INPUT', {
        data: obj,
      });

      navigate('/');
    }
  };

  return (
    <div>
      {/* <Button
        variant="contained"
        component="label"
        color="primary"
        size="large"
        onClick={() => {
          send('SAVE');
        }}
        disabled={state1.matches('saving')}
      >
        prepare data for export
      </Button> */}

      <br />
      <br />
      {/* {state1.matches('saving') && (
        <Button
          variant="contained"
          component="label"
          color="secondary"
          size="large"
          onClick={() => {
            send('SAVE');
          }}
        >
          <a
            href={state1.context.urlDataToSave}
            download="fintrack.json"
            onClick={() => {
              setTimeout(() => {
                send('SAVED');
              }, 500);
            }}
          >
            download
          </a>
        </Button>
      )} */}
      <Button
        variant="contained"
        component="label"
        color="primary"
        size="large"
        onClick={() => {
          function download(content: any) {
            var file = new Blob([content], {
              type: 'text/json;charset=UTF-8',
            });
            var reader = new FileReader();
            reader.onload = function () {
              // var popup = window.open();
              var link = document.createElement('a');
              link.setAttribute('href', reader.result as string);
              link.setAttribute('download', 'fintrack.json');
              // popup!.
              document.body.appendChild(link);
              link.click();
            };
            reader.readAsDataURL(file);
          }

          download(JSON.stringify(state1.context.db, null, 2));

          // setTimeout(() => {
          //   send('SAVED');
          // }, 500);
        }}
      >
        export
      </Button>

      <br />
      <br />

      <Button
        variant="contained"
        component="label"
        color="primary"
        size="large"
      >
        import
        <div style={{ display: 'none ' }}>
          <input type="file" onChange={handleFile} />
        </div>
      </Button>
    </div>
  );
};
