import { Machine, assign, send } from 'xstate';
import { FormContextValues } from 'react-hook-form';

interface FormSchema {
  states: {
    idle: {};
    validating: {};
    submitting: {};
    submitted: {};
  };
}

type FormEvent =
  | {
      type: 'SUBMIT';
    }
  | {
      type: 'REINIT';
    };

interface FormContext<T> {
  form: FormContextValues<T>;
}

export const formMachine = Machine<FormContext<any>, FormSchema, FormEvent>({
  id: 'form',
  initial: 'idle',
  states: {
    idle: {
      on: {
        SUBMIT: 'submitting',
      },
    },
    validating: {},
    submitting: {
      entry: assign((context, e) => {
        console.log(
          'e',
          e,
          '---context.form.getValues();',
          context.form.getValues(),
        );
        // send('SAVE', {
        //   to: 'store',
        // });
        console.log('======');
        return {};
      }),
      on: {
        '': 'submitted',
      },
      // onDone: {
      //   target: 'submitted'
      // }
      // entry: send('SAVE', {
      //   to: 'store',
      // })
    },
    submitted: {
      on: {
        'REINIT': 'idle',
      },
    },
  },
});
