import { string, array } from 'yup';

import {fintrack} from '../typings/fintrack'
import {ACCOUNTING_MONEY_FORMAT} from './constants'


const accounting = require('accounting');


export function formatMoney(
  amount: number | string,
  symbol: string = "",
  format: fintrack.AccountingMoneyFormat = ACCOUNTING_MONEY_FORMAT
) {
  const amountNumber = typeof amount === "number" ? amount : parseFloat(amount);
  return accounting.formatMoney(amountNumber, symbol, ...format)
}
