import { Machine, assign, sendParent } from 'xstate';

interface FetchSchema {
  states: {
    idle: {};
    loading: {};
    success: {};
    failure: {};
    fatal: {};
  };
}

interface FetchContext {
  url: string;
  data: any;
  error: any;
  retries: number;
  maxRetries: number;
}

type FetchEvent = { type: 'FETCH' } | { type: 'REFETCH' } | { type: 'RETRY' };

export const fetchMachine = Machine<FetchContext, FetchSchema, FetchEvent>(
  {
    id: 'fetch',
    initial: 'idle',
    context: {
      url: '',
      data: null,
      error: null,
      retries: 0,
      maxRetries: 3,
    },
    states: {
      idle: {
        on: {
          FETCH: 'loading',
        },
      },
      loading: {
        invoke: {
          src: 'fetch',
          onDone: {
            target: 'success',
            actions: ['setData', 'notifyData', sendParent('LOADED')],
          },
          onError: {
            target: 'failure',
            actions: ['setError', 'notifyError'],
          },
        },
      },
      success: {
        on: {
          REFETCH: {
            target: 'loading',
            actions: 'resetRetries',
          },
        },
      },
      failure: {
        on: {
          '': {
            target: 'fatal',
            cond: 'canNotRetry',
          },
          RETRY: {
            target: 'loading',
            actions: 'incrementRetries',
          },
        },
      },
      fatal: {
        type: 'final',
      },
    },
  },
  {
    guards: {
      canNotRetry: ({ retries, maxRetries }) => retries === maxRetries,
    },
    actions: {
      setData: assign({ data: (_, event) => (event as any).data }),
      notifyData: () => {
        console.log('-----notifydata')
      },

      setError: assign({ error: (_, event) => (event as any).data }),
      notifyError: () => {},

      incrementRetries: assign({ retries: context => context.retries + 1 }),
      // resetRetries: assign({ retries: 0 }),
    },
    services: {
      fetch: context =>
        fetch(context.url)
          .then(response => response.json())
          .then(json => json),
    },
  }
);
